<template>
  <v-layout>
    <v-container>
      <add-section-component v-on:getVal="getVal" />
      <center>
        <v-img
          v-if="!isGetData && !listOfSections.length"
          class="image-not-fond"
          contain
          :src="'/nodata.png'"
        >
        </v-img>
        <v-progress-circular
          id="progress"
          v-if="isGetData"
          :size="70"
          width="7"
          color="primary"
          indeterminate
        ></v-progress-circular>
      </center>
      <v-row>
        <card-component
          v-on:getVal="getVal"
          v-for="(dataOfSection, index) in listOfSections"
          :key="index"
          :dataSection="dataOfSection"
        />
      </v-row>
    </v-container>
  </v-layout>
</template>

<script>
import { sectionController } from "../../components/section/section";

export default {
  async mounted() {
    await this.getSection();
  },
  data() {
    return {
      isGetData: false,
      listOfSections: [],
    };
  },
  methods: {
    getVal: async function () {
      this.listOfSections = [];
      this.getSection();
    },
    getSection: async function () {
      this.isGetData = true;
      var data = await sectionController.getSections();
      this.isGetData = false;
      this.listOfSections = data;
    },
  },
  components: {
    "card-component": () => import("../../components/section/Card.vue"),

    // "list-sections": () => import("../components/section/ListSection.vue"),
    "add-section-component": () =>
      import("../../components/section/addSection.vue"),
  },
};
</script>

<style>
#progress {
  margin-top: 20%;
}
.image-not-fond {
  margin-top: 5%;
  height: 500px;
  width: 500px;
}
</style>


