

import axios from "axios";
import store from '../../store/index'

export const sectionController = {
    addSection: async function (file, section) {
        var state = false;
        var mainUrl = store.state.mainUrl;
        var formData = new FormData();
        formData.append("image", file);
        // formData.append("name", section.name);
        // formData.append("description", section.description);
        formData.append("areas", section.areas);
        formData.append("nation_id", section.nation_id);
        formData.append("description[en]", section.description.en);
        formData.append("description[ar]", section.description.ar);
        formData.append("description[tr]", section.description.tr);
        formData.append("name[en]", section.name.en);
        formData.append("name[ar]", section.name.ar);
        formData.append("name[tr]", section.name.tr);
        await axios
            .request({
                method: "POST",
                url: mainUrl + "/section",
                data: formData,
                headers: this.configAxios(),
            }).then(res => {

                if (res.status == 200) {
                    if (res.data.state) {
                        state = true;
                        this.ShowAlert('secondary', 'success add section ')
                    }
                }
                else {
                    state = false;
                    this.ShowAlert('red', 'An Error ' + res.status);
                }
            }).catch(error => {
                state = false;
                store.dispatch('showAlert', {
                    color: 'red', message: 'An Error ðŸ˜¥ please contact Us '
                        + error
                })
            })

        return state;
    },
    getAreas: async function () {
        var mainUrl = store.state.mainUrl;

        var areas = [];
        await axios.get(mainUrl + '/areas').then(data => {
            if (data.status == 200) {

                areas = data.data.data;
            } else {
                this.ShowAlert('red', 'An error  ERROR CODE ' + data.status);
            }

        }).catch(error => {
            this.ShowAlert('red', 'An ERROR ' + error)
        });
        return areas;
    },
    updateSection: async function (file, section, areas) {

        var state = false;
        var mainUrl = store.state.mainUrl;
        var formData = new FormData();
        formData.append("image", file);
        formData.append("areas", areas);
        formData.append("nation_id", section.nation_id);
        formData.append("description[en]", section.description.en);
        formData.append("description[ar]", section.description.ar);
        formData.append("description[tr]", section.description.tr);
        formData.append("name[en]", section.name.en);
        formData.append("name[ar]", section.name.ar);
        formData.append("name[tr]", section.name.tr);
        await axios
            .request({
                method: "POST",
                url: mainUrl + "/section/" + section.id,
                data: formData,
                headers: this.configAxios(),
            }).then(res => {
                if (res.status == 200) {
                    if (res.data.state) {
                        this.ShowAlert('secondary', 'done update success  ');
                        state = true;
                    }
                } else {
                    state = true;
                    this.ShowAlert('red', 'An error  : ' + res.status);
                }
            }).catch(error => {
                state = false;
                this.ShowAlert('red', 'An Error ðŸ˜¥ please contact Us '
                    + error);
            })

        return state;
    },

    deleteSection: async function (id) {
        var state = false;
        var mainUrl = store.state.mainUrl;
        await axios
            .request({
                url: mainUrl + "/section/" + id,
                method: "DELETE",
                headers: this.configAxios(),
            }).then(res => {
                if (res.status == 200) {
                    state = true;
                    this.ShowAlert('secondary', 'done delete success');
                }
            }).catch(error => {
                state = false
                this.ShowAlert('red', 'An Error ERROR CODE' + error);
            }
            );
        return state;
    },


    getSections: async function () {
        var data = [];
        await axios.get(store.state.mainUrl + '/section').then(response => {
            if (response.status == 200) {
                data = response.data.data;
                store.commit('SET_SECTIONS', data)
            }
        }).catch(function (error) {
            store.dispatch('showAlert', {
                color: 'red', message: 'An Error ðŸ˜¥ please contact Us '
                    + error
            })
        }
        );

        return data;

    },
    ShowAlert: function (color, message) {
        store.dispatch('showAlert', {
            color: color, message: message
        })
    },
    configAxios() {
        var token = store.state.token;
        return {
            "Content-type": "multipart/form-data",
            "Content-Disposition": "form-data",
            Authorization: "Bearer " + token,
        }
    }
}